<template>
  <div>
    <div class="m-container">
      <view-header title="系统首页"></view-header>
      <div class="m-table-div">
        <table>
          <thead>
            <th></th>
            <th>注册用户</th>
            <th>就诊人</th>
            <th>预约数</th>
            <th>取消数</th>
            <th>实际预约数</th>
          </thead>
          <tbody>
            <tr v-for="(n ,index) in syslist" :key="n">
              <td v-if=" index  === 0">今日新增</td>
              <td v-if=" index  === 1">昨日新增</td>
              <td v-if=" index  === 2">本周新增</td>
              <td v-if=" index  === 3">上周新增</td>
              <td v-if=" index  === 4">本月新增</td>
              <td v-if=" index  === 5">上月新增</td>
              <td v-if=" index  === 6">系统总数</td>
              <td>{{n.userCount}}</td>
              <td>{{n.patientCount}}</td>
              <td>{{n.subCount}}</td>
              <td>{{n.cancelSubCount}}</td>
              <td>{{n.realCount}}</td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  </div>
</template>
<script type="text/ecmascript-6">
import ViewHeader from 'base/view-header/view-header'
import {querySystem} from '@/api'
export default {
  components: {
    ViewHeader
  },
  mounted () {
    var _this = this
    var params = new URLSearchParams()
    querySystem(params).then(function (data) {
        if (!data.success) {
          alert(data.error)
        } else {
         _this.syslist = data.data
        }
      }).catch(function (data) {
        alert(data)
      })
  },
  data () {
    return {
      syslist:[]
    }
  }
}
</script>
<style lang="stylus" scoped rel="stylesheet/stylus">
@import "~common/stylus/base"
@import "~common/stylus/variable"
.m-container
  @extend .flex
  @extend .flex-column
  width: 100%
  height: 100%
  padding: 0 10px
</style>


